import React from "react";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet-async";

function OurMessage() {
  return (
    <div
      className="our-message border-bottom border-top border-2 bg-white py-5 my-5 overflow-hidden"
      id="our-message"
    >
      {/* Helmet SEO */}
      <Helmet>
        <title>  |      </title>
        <meta
          name="description"
          content="        :              ."
        />
        <meta
          name="keywords"
          content="  ,  ,  ,  ,  ,  ,  "
        />

        {/* OG Tags */}
        <meta
          property="og:title"
          content="  |      "
        />
        <meta
          property="og:description"
          content="               ."
        />
        <meta
          property="og:image"
          content="https://slimsmile.ps/assets/ourMessage.jpg"
        />
        <meta property="og:type" content="website" />
        <meta property="og:locale" content="ar_AR" />

        {/* Schema Markup */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "AboutPage",
            name: "  -   ",
            description:
              "             .",
          })}
        </script>
      </Helmet>

      {/* UI Content */}
      <div className="head text-center">
        <h2 className="mb-4 fs-1 fw-bolder" style={{ lineHeight: "1.8" }}>
           
        </h2>
        <div className="smile-container">
          <svg
            viewBox="0 0 200 100"
            xmlns="http://www.w3.org/2000/svg"
            style={{ width: "140px", marginTop: "-50px" }}
          >
            <defs>
              <linearGradient
                id="lineGradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="0%"
              >
                <stop offset="0%" stopColor="#2BA3DF" />
                <stop offset="100%" stopColor="#6dd5ed" />
              </linearGradient>
            </defs>

            <path
              d="M10,40 Q50,90 190,30"
              stroke="url(#lineGradient)"
              strokeWidth="8"
              fill="none"
              strokeLinecap="round"
            />
          </svg>
        </div>
      </div>

      <div className="container my-5">
        <div className="row align-items-center">
          {/*  */}
          <motion.div
            className="col-md-6 order-2 order-lg-1"
            style={{ color: "#222" }}
            initial={{ opacity: 0, x: 100 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.7 }}
          >
            {/* Vision */}
            <h4 className="fw-bold" style={{ color: "#00b3ff" }}>
               (Vision)
            </h4>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                        
                       
                    .
            </p>

            {/* Mission */}
            <h4 className="fw-bold mt-4" style={{ color: "#00b3ff" }}>
               (Mission)
            </h4>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
               <strong>  </strong>    
                       
                  .
            </p>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                         
                         
               .
            </p>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                        
                        .
            </p>
            <h5 className="mt-3 fw-bold" style={{ color: "#00b3ff" }}>
                   
            </h5>
          </motion.div>

          {/*  */}
          <motion.div
            className="col-md-6 justify-content-lg-end d-flex order-1 order-lg-2 mb-5 mb-md-0"
            initial={{ opacity: 0, x: -100 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.7 }}
          >
            <img
              src="assets/ourMessage.jpg"
              alt="      -   "
              className="img-fluid rounded-4"
              style={{ maxHeight: "450px", objectFit: "cover" }}
              loading="lazy"
              decoding="async"
              width="600"
              height="450"
            />
          </motion.div>
        </div>
      </div>
    </div>
  );
}

export default OurMessage;
