import React, { memo } from "react";
import { HashLink } from "react-router-hash-link";
import { Helmet } from "react-helmet-async";
import logo from "../images/logo-removebg-preview.png";

function Navbar() {
  return (
    <>
      {/* Helmet SEO */}
      <Helmet>
        <title> |      </title>
        <meta
          name="description"
          content="                    ."
        />
        <meta
          name="keywords"
          content=" ,  ,  ,  ,  ,  "
        />

        {/* OG Tags */}
        <meta
          property="og:title"
          content=" |      "
        />
        <meta
          property="og:description"
          content="          ."
        />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/logo-share.png"
        />
        <meta property="og:type" content="website" />
        <meta property="og:locale" content="ar_AR" />
      </Helmet>

      {/* UI Navbar */}
      <div dir="rtl">
        <nav
          className="navbar navbar-expand-lg navbar-light fixed-top py-0 bg-blur"
          style={{
            background: "rgba(255, 255, 255, 0.82)",
            backdropFilter: "blur(10px) saturate(120%)",
            WebkitBackdropFilter: "blur(10px) saturate(120%)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            boxShadow: "0 6px 24px rgba(16,24,40,0.08)",
          }}
        >
          <div className="container d-flex flex-row-reverse align-items-center justify-content-between">
            <HashLink smooth className="navbar-brand" to="/#home">
              <img
                src={logo}
                alt="   "
                width="50"
                height="50"
                loading="lazy"
                decoding="async"
              />
            </HashLink>

            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>

            <div
              className="collapse navbar-collapse justify-content-end"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav mb-2 mb-lg-0 ms-auto">
                {[
                  { label: "", to: "/#home" },
                  { label: " ", to: "/#about" },
                  { label: "", to: "/#services" },
                  { label: "", to: "/#team" },
                  { label: "", to: "/#activities" },
                  {
                    label: " ",
                    href: "https://reviews.saleemsmile.com",
                    external: true,
                  },
                  { label: " ", to: "/articles" },
                  { label: " ", to: "/#contact" },
                ].map((item, index) => (
                  <li className="nav-item" key={index}>
                    {item.external ? (
                      <a
                        className="nav-link fs-5"
                        href={item.href}
                        style={{ color: "#00ACE7" }}
                      >
                        {item.label}
                      </a>
                    ) : (
                      <HashLink
                        smooth
                        className={`nav-link fs-5 ${
                          item.to === "/#home" ? "active" : ""
                        }`}
                        to={item.to}
                        style={{
                          color: item.to === "/#home" ? "#0078d7" : "#00ACE7",
                        }}
                      >
                        {item.label}
                      </HashLink>
                    )}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </>
  );
}

export default memo(Navbar);
