import React from "react";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet-async";

function About() {
  return (
    <div className="about py-5 my-5 overflow-hidden" id="about">
      {/*  Helmet SEO */}
      <Helmet>
        <title>  |      </title>
        <meta
          name="description"
          content="       :                ."
        />
        <meta
          name="keywords"
          content="  ,  ,  ,  ,  , ,  ,  "
        />

        {/* OG Tags */}
        <meta
          property="og:title"
          content="  |      "
        />
        <meta
          property="og:description"
          content="                  ."
        />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/about.jpg"
        />
        <meta property="og:type" content="website" />
        <meta property="og:locale" content="ar_AR" />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "     ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "              .",
                },
              },
              {
                "@type": "Question",
                name: "     ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "             .",
                },
              },
              {
                "@type": "Question",
                name: "   ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "           .",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      {/* UI Content */}
      <div className="head text-center">
        <h2 className="mb-4 fs-1 fw-bolder" style={{ lineHeight: "1.8" }}>
           
        </h2>
        <div className="smile-container">
          <svg
            viewBox="0 0 200 100"
            xmlns="http://www.w3.org/2000/svg"
            style={{ width: "140px", marginTop: "-50px" }}
          >
            <defs>
              <linearGradient
                id="lineGradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="0%"
              >
                <stop offset="0%" stopColor="#2BA3DF" />
                <stop offset="100%" stopColor="#6dd5ed" />
              </linearGradient>
            </defs>

            <path
              d="M10,40 Q50,90 190,30"
              stroke="url(#lineGradient)"
              strokeWidth="8"
              fill="none"
              strokeLinecap="round"
            />
          </svg>
        </div>
      </div>

      <div className="container mt-5">
        <div className="row align-items-center">
          <motion.div
            className="col-md-6"
            initial={{ opacity: 0, x: 100 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.7 }}
          >
            <h3 className="py-2 fw-bold">  </h3>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                       
                       .
            </p>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                       
                      
                    .
            </p>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                          
                       
              .
            </p>
            <p style={{ fontSize: "1.1rem", lineHeight: "1.9" }}>
                       
                       .
            </p>
            <h5 className="mt-3 text-info fw-bold">
                  
            </h5>
            <a href="#contact" type="button" className="btn btn-info my-3 px-4">
                
            </a>
          </motion.div>

          <motion.div
            className="col-md-6 d-none d-md-block"
            initial={{ opacity: 0, x: -100 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.7 }}
          >
            <img
              src="/assets/about/about.jpg"
              alt="About Slim Smile"
              className="img-fluid rounded-4"
              loading="lazy"
              decoding="async"
              width="600"
              height="450"
              style={{ objectFit: "cover" }}
            />
          </motion.div>
        </div>
      </div>
    </div>
  );
}

export default About;
