import React from "react";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet-async";

function Activities() {
  return (
    <div
      className="activities my- py-5"
      id="activities"
      style={{ borderBottom: "1px solid #ccc", overflow: "hidden" }}
    >
      {/*  Helmet SEO */}
      <Helmet>
        <title>  |      </title>
        <meta
          name="description"
          content="                  ."
        />
        <meta
          name="keywords"
          content="   ,  ,  ,  ,   ,  ,  "
        />

        {/* OG Tags */}
        <meta
          property="og:title"
          content="  |      "
        />
        <meta
          property="og:description"
          content="               ."
        />
        <meta
          property="og:image"
          content="https://slimsmile.ps/images/activities-cover.jpg"
        />
        <meta property="og:type" content="article" />
        <meta property="og:locale" content="ar_AR" />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "       ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "                .",
                },
              },
              {
                "@type": "Question",
                name: "    ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "              .",
                },
              },
              {
                "@type": "Question",
                name: "       ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "               .",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      <div className="container">
        <div className="head text-center mb-4">
          <div className="d-flex flex-column align-items-center">
            <h2 className="mb-2 fs-1 fw-bolder"></h2>
            <svg
              viewBox="0 0 200 100"
              xmlns="http://www.w3.org/2000/svg"
              style={{ width: "140px", marginTop: "-30px" }}
            >
              <defs>
                <linearGradient
                  id="lineGradient"
                  x1="0%"
                  y1="0%"
                  x2="100%"
                  y2="0%"
                >
                  <stop offset="0%" stopColor="#2BA3DF" />
                  <stop offset="100%" stopColor="#6dd5ed" />
                </linearGradient>
              </defs>

              <path
                d="M10,40 Q50,90 190,30"
                stroke="url(#lineGradient)"
                strokeWidth="8"
                fill="none"
                strokeLinecap="round"
              />
            </svg>
          </div>
        </div>

        <div className="row">
          {/* TEXT */}
          <motion.div
            className="col-lg-6 col-md-12 text-end"
            style={{ letterSpacing: "1.2px", lineHeight: "30px" }}
            initial={{ opacity: 0, x: 100 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: false, amount: 0.3 }}
            transition={{ duration: 0.6, ease: "easeOut" }}
          >
            <p>
                  <strong> </strong>   
                       
                 .
            </p>
            <p>
                        
                       
              .          
                       .
            </p>
            <p>
                        
                         
               .
            </p>
            <p>
                        
                       
                    .
            </p>
          </motion.div>

          {/* VIDEO */}
          <motion.div
            className="video col-lg-6 col-md-12"
            initial={{ opacity: 0, x: -100 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: false, amount: 0.3 }}
            transition={{ duration: 0.6, ease: "easeOut" }}
          >
            <video
              className="rounded"
              muted
              autoPlay
              loop
              controls
              playsInline
              preload="metadata"
              style={{ width: "100%", maxHeight: "400px", objectFit: "cover" }}
            >
              <source src="/assets/video/active-video.mp4" type="video/mp4" />
                  .
            </video>
          </motion.div>
        </div>
      </div>
    </div>
  );
}

export default Activities;
