import React, { useState, useRef } from "react";
import { motion } from "framer-motion";
import { Helmet } from "react-helmet-async";

function Contact() {
  const [name, setName] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [email, setEmail] = useState("");
  const [treatmentType, setTreatmentType] = useState("");
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");

  const nameRef = useRef(null);
  const phoneNumberRef = useRef(null);
  const treatmentTypeRef = useRef(null);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (name === "" || phoneNumber === "" || treatmentType === "") {
      if (name === "") {
        nameRef.current.focus();
      } else if (phoneNumber === "") {
        phoneNumberRef.current.focus();
      } else if (treatmentType === "") {
        treatmentTypeRef.current.focus();
      }
      return;
    }
    setError("");

    const whatsappNumber = "+972599599591";
    const text = `: ${name}%0A : ${phoneNumber}%0A: ${
      email || " "
    }%0A : ${treatmentType}%0A: ${message || treatmentType}`;
    const url = `https://wa.me/${whatsappNumber}?text=${text}`;
    window.open(url, "_blank");
  };

  return (
    <motion.div
      className="container py-5"
      id="contact"
      initial={{ opacity: 0, scale: 0.5, y: 0 }}
      whileInView={{ opacity: 1, scale: 1, y: 0 }}
      viewport={{ once: false, amount: 0.3 }}
      transition={{ duration: 0.3, ease: "easeInOut" }}
    >
      {/*  SEO Helmet */}
      <Helmet>
        <title>  |    -  </title>
        <meta
          name="description"
          content="                     ."
        />
        <meta
          name="keywords"
          content=" ,   ,  ,   ,  ,  "
        />

        {/* OG Tags */}
        <meta property="og:title" content="  |   " />
        <meta
          property="og:description"
          content="                  ."
        />
        <meta
          property="og:image"
          content="https://via.placeholder.com/800x450.png?text=Contact+Slim+Smile"
        />
        <meta property="og:type" content="website" />
        <meta property="og:locale" content="ar_AR" />

        {/* FAQ Schema */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "FAQPage",
            mainEntity: [
              {
                "@type": "Question",
                name: "      ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "               .",
                },
              },
              {
                "@type": "Question",
                name: "    ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "           .",
                },
              },
              {
                "@type": "Question",
                name: "    ",
                acceptedAnswer: {
                  "@type": "Answer",
                  text: "               .",
                },
              },
            ],
          })}
        </script>
      </Helmet>

      <div className="head text-center">
        <div className="d-flex flex-column align-items-center">
          <h2 className="mb-2 fs-1 fw-bolder"> </h2>
          <svg
            viewBox="0 0 200 100"
            xmlns="http://www.w3.org/2000/svg"
            style={{ width: "140px", marginTop: "-30px" }}
          >
            <defs>
              <linearGradient
                id="lineGradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="0%"
              >
                <stop offset="0%" stopColor="#2BA3DF" />
                <stop offset="100%" stopColor="#6dd5ed" />
              </linearGradient>
            </defs>

            <path
              d="M10,40 Q50,90 190,30"
              stroke="url(#lineGradient)"
              strokeWidth="8"
              fill="none"
              strokeLinecap="round"
            />
          </svg>
        </div>
      </div>

      {error && <p className="text-danger">{error}</p>}

      <div className="row mt-5">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <div className="mb-3">
            <label htmlFor="name" className="form-label ms-2">
              
            </label>
            <input
              type="text"
              className="form-control"
              required
              id="name"
              placeholder=" "
              value={name}
              onChange={(e) => setName(e.target.value)}
              ref={nameRef}
            />
          </div>
          <div className="mb-3">
            <label htmlFor="phoneNumber" className="form-label ms-2">
               
            </label>
            <input
              type="number"
              className="form-control"
              required
              id="phoneNumber"
              placeholder=" "
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              ref={phoneNumberRef}
            />
          </div>
          <div className="mb-3">
            <label htmlFor="treatmentType" className="form-label ms-2">
               
            </label>
            <select
              className="form-select"
              required
              aria-label=" "
              value={treatmentType}
              onChange={(e) => setTreatmentType(e.target.value)}
              id="treatmentType"
              ref={treatmentTypeRef}
            >
              <option value="">--    --</option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=" "> </option>
            </select>
          </div>
        </div>

        <div className="col-lg-6 col-md-6 col-sm-12">
          <div className="mb-3">
            <label htmlFor="email" className="form-label ms-2">
              
            </label>
            <input
              type="email"
              className="form-control"
              id="email"
              placeholder="()  "
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="mb-3">
            <label htmlFor="message" className="form-label ms-2">
              
            </label>
            <textarea
              className="form-control py-4"
              id="message"
              rows="3"
              placeholder="  ..."
              value={message}
              onChange={(e) => setMessage(e.target.value)}
            ></textarea>
          </div>
        </div>

        <button
          onClick={handleSubmit}
          type="button"
          className="btn btn-info m-auto mt-3 text-dark"
          style={{ width: "fit-content" }}
        >
            
          <i className="fa-brands fa-whatsapp me-2 text-success"></i>
        </button>
      </div>
    </motion.div>
  );
}

export default Contact;
