import React from "react";
import { Link } from "react-router-dom";

function RelatedArticles() {
  const articles = [
    { name: " ", link: "/articles/cosmetic-fillings" },
    { name: " ", link: "/articles/dental-implants" },
    { name: " ", link: "/articles/guided-implant-surgery" },
    { name: "   ", link: "/articles/full-anesthesia-implant" },
    { name: "  ", link: "/articles/oral-health-and-heart" },
    { name: " ", link: "/articles/orthodontics" },
    { name: " ", link: "/articles/gum-diseases" },
    { name: " ", link: "/articles/dental-crowns" },
    { name: " ", link: "/articles/root-canal" },
    { name: "  ()", link: "/articles/decorative-veneers" },
    { name: "  ", link: "/articles/wisdom-teeth" },
  ];

  return (
    <div className="container my-4">
      <h5 className="fw-bold mb-3">  </h5>
      <div className="d-flex flex-wrap gap-2">
        {articles.map((article, index) => (
          <Link
            key={index}
            to={article.link}
            className="badge bg-light text-dark border"
            style={{ cursor: "pointer", fontSize: "12px" }}
          >
            #{article.name}
          </Link>
        ))}
      </div>
    </div>
  );
}

export default RelatedArticles;
